import React, { useState, useEffect } from 'react';
import { Link, useParams, useSearchParams, useLocation, useNavigate } from "react-router-dom";
import { useTranslation } from 'react-i18next';

import Global from '../../../infrastructure/Common/Global';
import Notifier from '../../../application/utils/Notifier';
import AdminApis from '../APIs/AdminApis';

import './blogs-n-news.scss';

const BlogPost = () => {
    const { t } = useTranslation();

    const params = useParams();
    const location = useLocation();
    const navigate = useNavigate();
    let [searchParams, setSearchParams] = useSearchParams();
    const baseURL = Global.baseUrl + "/";

    const initialPost = {
        id: '',
        page_id: '',
        title_en: '',
        title_bg: '',
        label: 'blog',
        author: '',
        content_en: '',
        content_bg: '',
        order_cms: '',
        slug: '',
        featured_url: '',
        content_type: 'image',
        sub_type: 'blog',
    }

    const [post, setPost] = useState(initialPost);
    const [postQuery, setPostQuery] = useState({ content_id: '', post_id: '' });
    const [isSaving, setIsSaving] = useState(false);
    const [isWaiting, setIsWaiting] = useState(false);
    const [statusLoader, setStatusLoader] = useState(false);
    const [action, setAction] = useState({ view: false, update: false });

    const [heroAttachment, setHeroAttachment] = useState({});
    const [customFields, setCustomFields] = useState([]);

    const [activeTab, setActiveTab] = useState('english');

    const postTabs = [
        { id: 'english', name: `${t('english')}` },
        { id: 'bulgarian', name: `${t('bulgarian')}` }
    ];

    useEffect(() => {
        // let preFilled = {};
        let query = { post_id: '' };

        // if (parseInt(params.content_id, 10)) {
        //     preFilled.page_id = params.content_id;
        // }

        // if (searchParams.get('postMeta')) {
        //     const urlData = JSON.parse(searchParams.get('postMeta'));
        //     preFilled.label = modifyString(urlData.title_en);
        // }

        if (params.post_id) {
            if (location.pathname.includes('view-post')) {
                console.log('PATH POST______', location.pathname);
                setAction({ view: true, update: false });
            } else {
                setAction({ view: false, update: true });
            }
            query.post_id = params.post_id;
            // if (params.content_id) {
            //     query.content_id = params.content_id;
            // }
            setPostQuery(query);
            getPost(query);
        }

        // Object.assign(post, preFilled);
        // setPost({ ...post });

    }, [navigate]);

    const modifyString = (label) => {
        return label.replace(/[^a-zA-Z ]/g, '').replace(/\s/g, '-').toLowerCase();
    }

    const handleChange = (e, item = null, subIndx = null, tIndex = null) => {
        const { name, value, checked } = e.target;
        if (item === 'custom-field') {
            let tempCf = [...customFields];
            tempCf[subIndx][name] = value;
            setCustomFields(tempCf);
        } else if (item === 'enable-hero') {
            setPost({ ...post, [name]: checked })
        } else {
            setPost({ ...post, [name]: value });
        }
    }

    const fileUpload = async (event) => {
        if (event.target.files.length > 0) {
            let tempPost = { ...post };
            let attachment = {
                fileName: event.target.files[0].name,
            }
            const fileType = event.target.files[0].type;
            console.log('FILE-TYPE', fileType);
            if (fileType.match(/image|video\/*/) == null) {
                attachment.error = 'File not supported.';
                await setHeroAttachment(attachment);
                return;
            } else {
                console.log('I-m in decision');
                let content_type = '';
                if (fileType.match(/image\/*/)) {
                    console.log('Got Image');

                    content_type = 'image';
                    // setPost({ ...post, content_type: 'image' });
                    // attachment.contentType = 'image';

                } else if (fileType.match(/video\/*/)) {
                    console.log('Got Video');

                    content_type = 'video';

                    // setPost({ ...post, content_type: 'video' });
                    // attachment.contentType = 'video'
                }

                console.log('Decision....Image/Video', content_type);

                // setPost({ ...post, content_type: content_type });
                tempPost.content_type = content_type;
                attachment.error = '';
            }

            const reader = new FileReader();
            reader.readAsDataURL(event.target.files[0]);
            reader.onload = () => {
                attachment.file = reader.result;
                setHeroAttachment(attachment);
                // setPost({ ...post, featured_url: reader.result })
            }
            tempPost.featured_url = event.target.files[0];

            await setPost(tempPost);


        }
    };

    const customFieldsFileUpload = async (event, index) => {
        console.log('IMAGE-EVENT....', event, index);

        if (event.target.files.length > 0) {
            let tempCustomFields = [...customFields];
            // let attachment = {
            //     fileName: event.target.files[0].name,
            // }
            const fileType = event.target.files[0].type;
            console.log('FILE-TYPE', fileType);
            if (fileType.match(/image\/*/) == null) {
                // attachment.error = 'File not supported.';
                // await setHeroAttachment(attachment);
                return;
            } else {
                console.log('Decision....Image');

                // setPost({ ...post, content_type: content_type });
                // tempCustomFields[index].content_type = content_type;
                // attachment.error = '';
            }

            const reader = new FileReader();
            reader.readAsDataURL(event.target.files[0]);
            reader.onload = () => {
                // tempCustomFields[index].file = event.target.files[0];
                tempCustomFields[index].file = reader.result;

                // const uploaded = uploadFieldFile(event.target.files[0]);
                const fd = new FormData();
                fd.append('attachment', event.target.files[0]);

                AdminApis.handleFileUpload(fd)
                    .then(res => {
                        // setIsSaving(false);
                        tempCustomFields[index].attachment = res.data.result;
                        console.log('SAVE-RESPONSE...', res.data);
                        setCustomFields(tempCustomFields);

                        // return res.data.result;
                    })
                    .catch(err => {
                        console.log(err);
                        // setIsSaving(false);
                    })

                // console.log('UPLOADED....', uploaded);
                // tempCustomFields[index].attach`ment = 'image source';
                // setPost({ ...post, featured_url: reader.result })

            }
        }

    }

    const uploadFieldFile = (file) => {
        const fd = new FormData();
        fd.append('attachment', file);
        AdminApis.handleFileUpload(fd)
            .then(res => {
                // setIsSaving(false);
                console.log('SAVE-RESPONSE...', res.data);
                return res.data.result;

                // navigate(-1);
                // setIsCreated(true);
                // NotifierService.notify('success', res.data.success);
                // navigate(params.content_id > 0 ? ('/admin/cms/posts/' + params.content_id) : '/admin/cms')
            })
            .catch(err => {
                console.log(err);
                // setIsSaving(false);
            })
    }

    const handleCustomFieldFile = (index = null) => {
        let tempCf = [...customFields];
        if (index > -1) {
            // temp.splice(index, 1);
            tempCf[index].attachment = '';
        }
        setCustomFields(tempCf);
    }

    const getPost = (query) => {
        setIsWaiting(true);

        AdminApis.postDetail(query)
            .then(res => {
                const data = res.data.result;
                if (data.custom_fields) {
                    setCustomFields(JSON.parse(data.custom_fields));
                }
                
                setIsWaiting(false);
                setPost(data);
                if (res.data.result.featured_url) {
                    // let attachment = {
                    //     fileName: event.target.files[0].name,
                    // }
                    console.log('FEATURED_URL...', baseURL + res.data.result.featured_url);
                    setHeroAttachment({ file: (baseURL + res.data.result.featured_url) })
                    // this.featuredAttachmentUrl = this.baseUrl + '/' + response.result.featured_url;
                }
                // setIsCreated(true);
                // NotifierService.notify('success', res.data.success);
                // navigate(params.content_id > 0 ? ('/admin/cms/posts/' + params.content_id) : '/admin/cms')
            })
            .catch(err => {
                console.log(err);
                setIsWaiting(false);
            })
    }

    const savePost = () => {
        setIsSaving(true);
        // const type = update ? 'update' : 'create';
        const type = action.update ? 'update' : 'create';
        const data = getFormData();
        AdminApis.savePost(type, data)
            .then(res => {
                setIsSaving(false);
                console.log('SAVE-RESPONSE...', res.data);
                // navigate(-1);
                // setIsCreated(true);

                Notifier.notify('success', res.data.success);
                // navigate(params.content_id > 0 ? ('/admin/cms/posts/' + params.content_id) : '/admin/cms')
            })
            .catch(err => {
                console.log(err);
                setIsSaving(false);
            })
    }

    const handleCustomField = (index = null) => {
        let temp = [...customFields];

        console.log('CUSTOM-FIELD');
        if (index > -1) {
            temp.splice(index, 1);
        } else {
            temp.push({
                key: '',
                value: '',
            });
        }
        setCustomFields(temp);
    }

    const getFormData = () => {
        const formData = new FormData();


        formData.append('post_type', post.page_id ? 'post' : 'cms');
        formData.append('page_id', post.page_id ? post.page_id : '');
        formData.append('title_en', post.title_en);
        formData.append('title_bg', post.title_bg);
        formData.append('label', post.label);
        formData.append('content_en', post.content_en);
        formData.append('content_bg', post.content_bg);
        formData.append('order_cms', post.order_cms ? post.order_cms : '');
        formData.append('content_type', post.content_type);
        formData.append('featured_url', post.featured_url);
        formData.append('author', post.author);
        formData.append('post_type', post.post_type === 'parent-blog' ? 'parent-blog' : 'blog');
        formData.append('sub_type', post.sub_type);
        
        // formData.append('post_type', this.postContent.page_id ? 'blog' : 'parent-blog');

        formData.append('custom_fields', JSON.stringify(customFields.map(({ file, ...rest }) => rest)));

        // formData.append('hero_enable', ~~post.hero_enable);
        // formData.append('hero_title_en', post.hero_title_en);
        // formData.append('hero_title_bg', post.hero_title_bg);
        // formData.append('hero_desc_en', post.hero_desc_en);
        // formData.append('hero_desc_bg', post.hero_desc_bg);

        const formObj = {
            post_type: post.page_id ? 'post' : 'cms',
            page_id: post.page_id ? post.page_id : '',
            title_en: post.title_en,
            title_bg: post.title_bg,
            label: post.label,
            content_en: post.content_en,
            content_bg: post.content_ng,
            order_cms: post.order_cms ? post.order_cms : '',
            content_type: post.content_type,
            featured_url: post.featured_url,
            hero_enable: ~~post.hero_enable,
            hero_title_en: post.hero_title_en,
            hero_title_bg: post.hero_title_bg,
            hero_desc_en: post.hero_desc_en,
            hero_desc_bg: post.hero_desc_bg,
            custom_fields: customFields,
        }

        if (action.update) {
            formData.append('id', post.id ? post.id : postQuery.post_id);
            formObj.id = post.id ? post.id : postQuery.post_id;
        }

        console.log(formObj);

        return formData;
    }

    const updatePostStatus = () => {
        setStatusLoader(true);
        const payload = { post_id: postQuery.post_id, status: post.status === 'publish' ? 'pending' : 'publish' };

        AdminApis.updatePostStatus(payload)
            .then(res => {
                setStatusLoader(false);
                console.log('SAVE-RESPONSE...', res.data);
                let alertMsg = ''
                if (res.data.result === 'publish') {
                    setPost({ ...post, status: res.data.result });
                    alertMsg = 'Post Published.';
                } else {
                    alertMsg = 'Post Deactivated.';
                    setPost({ ...post, status: res.data.result });
                }
                // navigate();
                // setIsCreated(true);
                Notifier.notify('success', alertMsg);
                // navigate(params.content_id > 0 ? ('/admin/cms/posts/' + params.content_id) : '/admin/cms')

            })
            .catch(err => {
                console.log(err);
                setStatusLoader(false);
            })
    }

    return (
        <>
            <div className="d-block d-md-flex align-items-end justify-content-between mb-4">
                <div className="section-header mb-3 mb-md-0">
                    <div className="primary">{t('blog_n_news')}</div>
                    <h5 className="title fw-medium mb-0">{action.update ? t('edit_post') : (action.view ? t('view_post') : t('add_new_post'))}</h5>
                </div>
            </div>

            <div className="row">
                <div className="col-lg-9">
                    <section className="section-data p-3">
                        {isWaiting ?
                            <div className="content-placeholder">
                                <h5 className="placeholder-glow mb-4">
                                    <span className="placeholder col-6"></span>
                                </h5>
                                <p className="card-text placeholder-glow">
                                    <span className="placeholder col-4 mt-3"></span>
                                    <span className="placeholder col-12"></span>
                                    <span className="placeholder col-4 mt-4"></span>
                                    <span className="placeholder col-12"></span>
                                    <span className="placeholder col-12"></span>
                                    <span className="placeholder col-10"></span>
                                    <span className="placeholder col-8"></span>
                                    <span className="placeholder col-7"></span>
                                    <span className="placeholder col-7"></span>
                                    <span className="placeholder col-6"></span>
                                </p>
                            </div>
                            :

                            <div className="nodis-tabs-container">

                                <div className="nodis-tabs d-flex">
                                    {postTabs.map((tab, index) => (
                                        <div className={'tab-item me-4 ' + (tab.id === activeTab ? 'active' : '')} key={'tab-' + (index + 1)} onClick={() => setActiveTab(tab.id)}>
                                            {tab.name}
                                        </div>
                                    ))}
                                </div>

                                <div className="tabs-content mt-4">
                                    {activeTab === 'english' &&
                                        <div className="row">
                                            <div className="col-md-12">
                                                <div className="mb-4">
                                                    <label htmlFor="title_en" className="form-label label-text">
                                                        {t('title')}
                                                    </label>

                                                    <input
                                                        id="title_en"
                                                        type="text"
                                                        className="form-control nodis-field"
                                                        name="title_en"
                                                        placeholder={t('your_post_title') + '...'}
                                                        value={post.title_en || ''}
                                                        onChange={handleChange}
                                                        autoFocus
                                                        disabled={action.view}
                                                    />
                                                </div>
                                            </div>

                                            <div className="col-md-12 mb-3">
                                                <label htmlFor="content_en" className="form-label field-label">{t('post_content')}</label>
                                                <textarea
                                                    id="content_en"
                                                    className="form-control nodis-field"
                                                    cols="80"
                                                    rows="5"
                                                    placeholder={t('write_content') + '...'}
                                                    name="content_en"
                                                    value={post.content_en || ''}
                                                    onChange={handleChange}
                                                    disabled={action.view}
                                                ></textarea>
                                            </div>

                                        </div>
                                    }

                                    {activeTab === 'bulgarian' &&
                                        <div className="row">
                                            <div className="col-md-12">
                                                <div className="mb-4">
                                                    <label htmlFor="title_bg" className="form-label label-text">
                                                        {t('title')}
                                                    </label>

                                                    <input
                                                        id="title_bg"
                                                        type="text"
                                                        className="form-control nodis-field"
                                                        name="title_bg"
                                                        placeholder={t('your_post_title') + ' | BG ...'}
                                                        value={post.title_bg || ''}
                                                        onChange={handleChange}
                                                        autoFocus
                                                        disabled={action.view}
                                                    />
                                                </div>
                                            </div>

                                            <div className="col-md-12 mb-3">
                                                <label htmlFor="content_bg" className="form-label field-label">{t('post_content')}</label>
                                                <textarea
                                                    id="content_bg"
                                                    className="form-control nodis-field"
                                                    cols="80"
                                                    rows="5"
                                                    placeholder={t('write_content') + ' | BG ...'}
                                                    name="content_bg"
                                                    value={post.content_bg || ''}
                                                    onChange={handleChange}
                                                    disabled={action.view}
                                                ></textarea>
                                            </div>

                                        </div>
                                    }
                                </div>

                            </div>
                        }
                    </section>

                    <section className="section-data p-3 mt-3">
                        <div className="min-h-150">
                            <div className="title">{t('media')}</div>

                            <div className="media-content mt-3 d-block d-md-flex align-items-start">
                                {!action.view &&
                                    <label htmlFor="file-input" className="nodis-file-upload btn nodis-btn outline text-nowrap me-md-3">
                                        {t('upload_new')}
                                        <input
                                            type="file"
                                            id="file-input"
                                            accept="image/*, video/*"
                                            onChange={fileUpload}
                                            style={{ display: 'none' }}
                                        />
                                    </label>
                                }

                                {((post.content_type === 'image' || post.content_type === null) && post.featured_url) &&
                                    <div className="img-preview mt-3 mt-md-0">
                                        <div className="p-2 w-100 h-100">
                                            <div className="img-container" style={{ backgroundImage: `url(${heroAttachment.file})` }}></div>
                                        </div>
                                        {heroAttachment.fileName &&
                                            <div className="preview-img-title">
                                                {heroAttachment.fileName}
                                            </div>
                                        }
                                    </div>
                                }
                            </div>
                        </div>
                    </section>
                </div>

                <div className="col-lg-3">
                    <section className="section-data p-3">
                        <div className="min-h-150 d-flex flex-column">
                            {/* <div className="title">{t('actions')}</div> */}

                            <div className="mb-3">
                                <label htmlFor="author" className="form-label field-label mb-1">
                                    {t('author')}
                                    {/* <span className="small">(*usually parent post title for categorization)</span>  */}
                                </label>
                                <input
                                    type="text"
                                    className="form-control nodis-field"
                                    name="author"
                                    placeholder={t('author') + '...'}
                                    value={post.author || ''}
                                    onChange={handleChange}
                                    disabled={action.view}
                                />
                            </div>

                            <div className="mb-3">
                                <label htmlFor="sub_type" className="form-label field-label mb-1">
                                    {t('category')}
                                    {/* <span className="small">(*usually parent post title for categorization)</span>  */}
                                </label>
                                {/* <input
                                    type="text"
                                    className="form-control nodis-field"
                                    name="author"
                                    placeholder={t('author') + '...'}
                                    value={post.author || ''}
                                    onChange={handleChange}
                                    disabled={action.view}
                                /> */}
                                <select onChange={handleChange} name="sub_type" value={post.sub_type} disabled={action.view} className="form-control nodis-field px-2">
                                    <option value="">{t('select_category')}</option>
                                    {/* {languages.map((language) => ( */}
                                        <option value='blog'>{t('blog')}</option>
                                        <option value='news'>{t('news')}</option>
                                    {/* ))} */}
                                </select>
                            </div>



                            <div className="mb-3">
                                <label htmlFor="label" className="form-label field-label mb-1">
                                    {t('label')}
                                    {/* <span className="small">(*usually parent post title for categorization)</span>  */}
                                </label>
                                <input
                                    type="text"
                                    className="form-control nodis-field"
                                    name="label"
                                    placeholder={t('label') + '...'}
                                    value={post.label || ''}
                                    onChange={handleChange}
                                    disabled={action.view}
                                />
                            </div>

                            <div className="">

                                <label htmlFor="order" className="form-label field-label mb-1">
                                    {t('data_order')}
                                </label>
                                <input
                                    type="number"
                                    className="form-control nodis-field"
                                    name="order_cms"
                                    placeholder={t('data_order') + '...'}
                                    value={post.order_cms || ''}
                                    min={0}
                                    onChange={handleChange}
                                    disabled={action.view}
                                />
                            </div>

                        </div>
                    </section>

                    <section className="section-data p-3 mt-3">
                        <div className="min-h-150 d-flex flex-column">
                            <div className="title">{t('actions')}</div>

                            <div className="action-item mt-2">
                                <span className="fw-medium text-dim">{t('status')}: </span>
                                <span className={'capitalize ' + (post?.status === 'publish' ? 'success' : 'warning')}>{t('status_' + (post?.status || 'pending'))}</span>
                            </div>

                            <div className="mt-auto">
                                {(!isWaiting && !post.id) &&
                                    <button type="submit" className="btn nodis-btn success btn-block w-100 mt-auto" onClick={savePost} disabled={isSaving}>
                                        {isSaving &&
                                            <div className="button-loader">
                                                {t('saving')}...
                                            </div>
                                        }
                                        {t('save_n_publish')}
                                    </button>
                                }

                                {(!isWaiting && action.view) &&
                                    <button
                                        className="btn nodis-btn info btn-block w-100 mt-auto"
                                        onClick={() => navigate(`/admin-portal/cms/edit-post/${postQuery.content_id}/${postQuery.post_id}`)}
                                    >
                                        {t('edit_post')}
                                    </button>
                                }

                                {(!isWaiting && action.update) &&
                                    <div>
                                        <button type="submit" className={"btn nodis-btn success btn-block w-100 mt-auto mb-2 " + (post.status === 'publish' ? 'danger' : 'info')} onClick={updatePostStatus} disabled={statusLoader}>
                                            {statusLoader &&
                                                <div className="button-loader">
                                                    {t('saving')}...
                                                </div>
                                            }
                                            {post.status === 'publish' ? t('deactivate_post') : t('publish_post')}
                                        </button>

                                        {/* <button type="submit" className="imparo-btn danger-button btn-block w-100 mb-3" onClick={savePost}>
                                            {isSaving &&
                                                <div className="button-loader"><img src="/loader.svg" alt="Loading" /></div>
                                            }
                                            Delete Post
                                        </button> */}

                                        <button type="submit" className="btn nodis-btn success btn-block w-100 mt-auto" onClick={savePost} disabled={isSaving}>
                                            {isSaving &&
                                                <div className="button-loader">
                                                    {t('saving')}...
                                                </div>
                                            }
                                            {t('save_changes')}
                                        </button>

                                    </div>
                                }

                            </div>
                        </div>
                    </section>

                    {/* <pre>
                        {JSON.stringify(post, 0, 2)}
                    </pre> */}
                </div>
            </div>
        </>
    );
};

export default BlogPost;